export default {
	data() {
		return {
			baseUrl: '',
			statu: false,
			payChoose: false,
			hide: false,
			alipy: true,
			wxpay: false,
			userName: '',
			days: '',
			userImg: '',
			mark: false,
			markas: false,
			cookile: '',
			iconcheck: 0, //头像是否改变
			image: '' //默认头像
		};
	},
	onShow() {
		
		uni.setNavigationBarTitle({
				title: this.$t('index.navTitleFour')
			})	
	uni.setTabBarItem({
		index:0,
	    text: this.i18n.navTitle
	});
	uni.setTabBarItem({
		index:2,
	    text: this.i18n.navTitleTwo
	});
	uni.setTabBarItem({
		index:1,
	    text: this.i18n.navTitleThree
	});
	uni.setTabBarItem({
		index:3,
	    text: this.i18n.navTitleFour
	});
		// console.log('now:' + this.websiteUrl);
		this.baseUrl = this.websiteUrl;
		var num = uni.getStorageSync('num');
		let token = uni.getStorageSync('storage_token');
		//  判断是否登录
		if (token == '' || token == null || token == undefined) {
			token = this.statu = false;
			this.mark = false;
		} else {
			token = this.statu = true;
			if (num == 1) {
				this.mark = true;
				this.markas = true;
			} else {
				this.mark = false;
				this.markas = false;
			}
		}
		console.log(num);

		// 用户信息
		this.userinfo();
		this.cookie();
	},
	computed: {
		i18n() {
			return this.$t('index');
		}
	},
	methods: {
		cookie() {
			this.baseUrl = this.websiteUrl;
			let token = uni.getStorageSync('storage_token');
			console.log(this.baseUrl);
			console.log(token);
			uni.request({
				url: this.baseUrl + '/api/config',
				method: 'get',
				data: {
					type: 'cookie_alert'
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					token: token
				},
				success: res => {
					console.log(res.data.data);
					let remarkStr = '';
					// this.cookile = res.data.data;
					const system_info = uni.getStorageSync('system_info');
					console.log(system_info.language);
					if(system_info.language == "zh_CN" || system_info.language == "zh-CN"){
						remarkStr = res.data.data.cn.replace('\g','')
						this.cookile = remarkStr;
					}else {
						remarkStr = res.data.data.en.replace('\g','')
						this.cookile = remarkStr;
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		queren() {
			uni.setStorage({
				key: 'num',
				data: '2',
				success: function() {
					console.log('success');
				}
			});
			this.mark = false;
			this.markas = false;
		},
		quxiao() {
			uni.setStorage({
				key: 'num',
				data: '2',
				success: function() {
					console.log('success');
				}
			});
			this.mark = false;
			this.markas = false;
		},
		userinfo() {
			var that = this;
			that.baseUrl = that.websiteUrl;
			let token = uni.getStorageSync('storage_token');

			uni.request({
				url: that.baseUrl + '/api/userInfo',
				method: 'GET',
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					token: token
				},
				success: function(res) {
					if (res.data.code == 200) {
						console.log(res.data); 
						// console.log(res.data.data.account);
						// console.log(res.data.data.vip_balance_days);
						// console.log(res.data.data.portrait);
						that.userName = res.data.data.account;
						that.days = res.data.data.vip_balance_days || 0;
						that.image = res.data.data.portrait;
					}
				}
			});
		},
		upload() {
			let _self = this;
		
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: function(res) {
					const tempFilePaths = res.tempFilePaths;
					_self.image = tempFilePaths[0];
					// console.log('tempFilePaths[0]', tempFilePaths[0]); //能够打印出选中的图片
					console.log(_self.image);
					_self.iconcheck = 1; //点击后图片更改状态由0变成1
					_self.touxiang(_self.image);	
				},
				error: function(e) {
					console.log(e);
				}
			});
		},
		touxiang(path) {
			let token = uni.getStorageSync('storage_token');
			console.log(token);
			uni.uploadFile({
				url: this.baseUrl + '/api/uploadFile',
				method: 'POST',
				formData: {
					file: path
				},
				filePath: path,
				name: 'file',
				header: {
					token: token
				},
				success: res => {
					console.log(res.data);
					console.log(JSON.parse(res.data).code);
					let img = JSON.parse(res.data).data.img_path;
					// let img = res.data.data.img_path;
					console.log(img);
					uni.showToast({
						title: JSON.parse(res.data).msg,
						icon: 'none',
						duration: 800
					});
					console.log("22222222222");
					console.log(img);
					this.shangChuanImg(img);
					
				},
				fail: () => {},
				complete: () => {}
			});
		},
		shangChuanImg(img) {
			var that = this;
			let token = uni.getStorageSync('storage_token');
			// console.log(path);
			uni.request({
				url: this.baseUrl + '/api/portrait',
				method: 'POST',
				data: {
					path: img
				},
			
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					token: token
				},
				success: res => {
					console.log(res.data);
					if (res.data.code == '200') {
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 800
						});
						that.userinfo();
					} else {
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 800
						});
						return false;
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		renzheng() {
			uni.navigateTo({
				url: '../renzheng/renzheng'
			});
		},
		gerenClick() {
			this.wxpay = true;
			this.alipy = false;
		},
		qiyeClick() {
			this.wxpay = false;
			this.alipy = true;
		},
		buyNum() {
			this.payChoose = true;
			this.hide = true;
		},
		sure() {
			// this.flag = false;
			var alipy;
			if (this.alipy == true) {
				alipy = '2';
			} else {
				alipy = '1';
			}
			console.log(alipy);
			this.baseUrl = this.websiteUrl;
			let token = uni.getStorageSync('storage_token');
			// pay_type  1/微信 2/支付宝  3/小程序
			// type 1/VIP 2/超出后购买 3/自动续费 4/会员免费单词查询
			uni.request({
				url: this.baseUrl + '/api/vipOrder',
				method: 'POST',
				data: {
					pay_type: alipy,
					type: '3'
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					token: token
				},
				success: res => {
					if (res.data.code == 200) {
						console.log(res.data);
						// console.log(res.data.data.payData);
						// orderInfo 微信/支付宝订单数据
						// provider:provider  wxpay/微信 alipay支付宝

						let orderInfo = res.data.data.payData;
						console.log(orderInfo);
						uni.requestPayment({
							provider: 'alipay',
							orderInfo: orderInfo,
							success: function(res) {
								console.log('success:' + JSON.stringify(res));
							},
							fail: function(err) {
								console.log('fail:' + JSON.stringify(err));
							}
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		cancel() {
			this.hide = false;
			this.payChoose = false;
		},
		account() {
			uni.navigateTo({
				url: '../account/account'
			});
		},
		query() {
			uni.navigateTo({
				url: '../query/query'
			});
		},
		set() {
			uni.navigateTo({
				url: '../set/set'
			});
		},
		yitihua() {
			uni.navigateTo({
				url: '../yitihua/yitihua'
			});
		},
		order() {
			uni.navigateTo({
				url: '../order/order'
			});
		},
		shouCang() {
			uni.navigateTo({
				url: '../shoucang/shoucang'
			});
		},
		login() {
			uni.navigateTo({
				url: '../login/login'
			});
		},
		// out() {
		// 	// 删除token
		// 	uni.removeStorageSync('storage_token');
		// 	// console.log(uni.getStorageSync('storage_token'));
		// 	uni.showToast({
		// 		title: '操作成功',
		// 		icon: 'none',
		// 		duration: 800
		// 	});
		// 	uni.navigateTo({
		// 		url: '../login/login'
		// 	});
		// },
		// xieyi() {
		// 	uni.navigateTo({
		// 		url: '../yonghuxieyi/yonghuxieyi'
		// 	});
		// },
		// zhengce() {
		// 	uni.navigateTo({
		// 		url: '../yinsizhengce/yinsizhengce'
		// 	});
		// },
		// us() {
		// 	uni.navigateTo({
		// 		url: '../aboutUS/aboutUS'
		// 	});
		// }
	}
};
